.header{
  width: 100%;
}
.header .header_icons{
  background-color: #000;
}
.header_icons .header_icons_contact{
  padding: 0.5em;
  text-align: center;
}
.header_icons .header_icons_contact a{
  color: #fff;
}
.header_icons .header_icons_social{
  width: 100%;
  padding: 0.5em;
  text-align: center;
}
.header .header_icons_social i{
  font-size: 20px;
  width: 30px;
  text-align: center;
  padding: 5px 0;
  background-color: rgba(225, 225, 225,0.5);
  color: #fff;
  margin: 0 0.1em 0;
  transition: all 500ms ease-in-out;
}
.header .header_icons_social i:hover{
  font-size: 22px;
  background-color: rgba(225, 225, 225,1);
  color: #000;
  padding: 3px 0;
}

.header_main{
  color: #fff;
  padding: 0em;
  position: relative;
  width: 100%;
}
.header_text{
  width: 100%;
  position: absolute;
  top: 50%;
  font-weight: bolder;
  text-shadow: 2px 2px 2px #000, -2px -2px 2px #000;
}
.header_main img{
  width: 100%;
  z-index: -1;
}
.main_nav{
  position: relative;
  color: #000;
  padding: 1em;
}
.main_nav .menu{
  display: flex;
  justify-content: space-between;
  padding: 0.5em;
 
  background-color: #fff;
  color: #000;
  margin: 0.5em 0;
}
.fa-caret-down{
  display: none;
}
.main_nav ul{
  list-style-type: none;
  margin: 0;
  padding: 0;
  background-color: #fff;
  border: 1px solid #000;
  position: absolute;
  top: 55px;
  width: 92%;
  z-index: 200;
  display: none;
}
.main_nav .display{
  display: block;
}
.main_nav .nodisplay{
  display: none;
}
.main_nav ul li a{
  display: block;
  padding: 0.5em;
  color: #000;
  transition: 300ms;
  text-decoration: none;
}
.main_nav ul li a:hover{
  background-color: black;
  color: #fff;
}

